.App {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  color: white;
}

.App-header {
  background-color: #222;
  height: 10vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-title {
  font-size: 1.5em;
  margin: 0;
}

.cell{
  height: 25%;
  width: 25%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}

.full-cell{
  background: black;
}

.shake {
  -webkit-animation: shake;
          animation: shake;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
}

@-webkit-keyframes shake {
  50% { -webkit-transform: scale(0.95,0.95); transform: scale(0.95,0.95);}
}

@keyframes shake {
  50% { -webkit-transform: scale(0.95,0.95); transform: scale(0.95,0.95);}
}

.empty-cell{
  background: white;
}

#board{
  width: 400px;
  height: 400px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.row {
  display: -ms-flexbox;
  display: flex;
  background: rgb(140, 47, 228);
  -ms-flex-pack: center;
      justify-content: center;
}

.row-btns {
  height: 15vh;
  -ms-flex-align: center;
      align-items: center;
}

.row-board{
  height: 75vh;
}

button {
  height: 6vh;
  font-family: 'Bungee', cursive;
  border-radius: 10%;
  margin: 0% 2%;
  background: rgb(100, 77, 77);
  width: 8%;
  color:white;
}

@media only screen and (max-width: 600px) {
  button {
      width: 40%;
  }
}

/* Modal*/
.myModal {
  display: none;
  z-index: 1;
  position: fixed;
  background-image: url("https://media.giphy.com/media/zaDi0mXkYM3eg/giphy.gif");
  background-size: cover;
  background-position: center;
  border: 1px solid #888;
  width: 50%;
  height: 65vh;
  font-family: 'Bungee Shade', cursive;
  font-size: 5em;
  padding:1%;
  text-align: center;
  border-radius: 3%;
}

.myModal p {
  margin:0;
  background: black;
  opacity: 0.5;
  border-radius: 5%;
}

@media only screen and (max-width: 600px) {
  .myModal {  
    width: 100%;
    font-size: 3em;
  }
}
/* The Close Button */
.close {
  color:#000;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  font-size: 0.4em;
  font-weight: bold;
}

@media only screen and (max-width: 600px) {
  .close {
    font-size: 0.7em;
    margin-right: 3%;
  }
}

.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}


